<template>
  <v-card color="#303030" v-if="this.e6 == 4" flat>
    <v-card-title class="justify-center headline text--orange">{{price}} {{sumPrice}}€</v-card-title>
    <v-card-subtitle
      v-if="this.e6 == 4"
      class="justify-center text--orange"
    >Solltest du noch Fragen zu den einzelnen Tabaksorten haben, kannst du jederzeit auf das Personal zugehen.</v-card-subtitle>
  </v-card>
  <v-stepper v-else flat color="primary" v-model="e6" vertical>
    <v-card flat color="black">
      <v-card-title class="justify-center headline text--orange">{{price}} {{sumPrice}}€</v-card-title>
      <v-card-subtitle
        v-if="this.e6 == 4"
        class="justify-center text--orange"
      >Du kannst dir jetzt noch ein passendes Getränk suchen.</v-card-subtitle>
    </v-card>
    <v-stepper-step
      v-if="this.e6 < 4"
      color="primary"
      :complete="e6 > 1"
      step="1"
    >Wähle deine Shisha</v-stepper-step>

    <v-stepper-content step="1">
      <v-card flat color="primary" class>
        <v-container fluid>
          <v-row dense>
            <v-col cols="6">
              <v-card @click="e6 = 2; stepPrice1=11; isModern=false">
                <v-img
                  :src="images.orientalisch"
                  class="white--text align-center"
                  gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)"
                  height="150px"
                >
                  <v-card-title>Oriental</v-card-title>
                  <v-card-subtitle>+11€</v-card-subtitle>
                </v-img>
              </v-card>
            </v-col>
            <v-col cols="6">
              <v-card @click="e6 = 2; stepPrice1=13; isModern=true">
                <v-img
                  :src="images.modern"
                  class="white--text align-center"
                  gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)"
                  height="150px"
                >
                  <v-card-title>Modern</v-card-title>
                  <v-card-subtitle>+13€</v-card-subtitle>
                </v-img>
              </v-card>
            </v-col>
          </v-row>
        </v-container>
      </v-card>
    </v-stepper-content>

    <v-stepper-step v-if="this.e6 < 4" :complete="e6 > 2" step="2">Wähle deinen Tabak (20g)</v-stepper-step>

    <v-stepper-content step="2">
      <v-card color="black">
        <v-card-subtitle>
          <v-icon class="mr-2">mdi-information</v-icon>Extrakopf = Service-Pauschale 3€ + Tabakpreis
        </v-card-subtitle>
        <v-list color="black" shaped>
          <v-list-item-group color="secondary">
            <v-list-item
              @click="e6 = 4; stepPrice2=tag.price"
              v-for="tag in currentPrice "
              :key="tag.price"
            >
              <v-list-item-content>
                <v-btn outlined>
                  <v-list-item-subtitle class="text--primary" v-text="tag.name"></v-list-item-subtitle>
                  <v-list-item-action-text>+{{tag.price}}€</v-list-item-action-text>
                </v-btn>
              </v-list-item-content>
            </v-list-item>
          </v-list-item-group>
        </v-list>
        <v-btn color="primary" @click="reset() ">Zurück</v-btn>
      </v-card>
    </v-stepper-content>

    <v-stepper-step v-if="this.e6 < 4" :complete="e6 > 3" step="3">Wähle dein Extra</v-stepper-step>

    <v-stepper-content v-if="this.e6 < 4 " step="3">
      <v-card color="black">
        <v-container fluid>
          <v-row dense>
            <v-col cols="6">
              <v-card @click="stepPrice3=3;e6 = 4">
                <v-img
                  :src="images.ice"
                  class="white--text align-center"
                  gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)"
                  height="150px"
                >
                  <v-card-subtitle>Bazooka</v-card-subtitle>
                  <v-card-subtitle>+3€</v-card-subtitle>
                </v-img>
              </v-card>
            </v-col>
            <v-col cols="6">
              <v-card @click=" stepPrice3=3+stepPrice2; e6 = 4 ">
                <v-img
                  :src="images.ice"
                  class="white--text align-center"
                  gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)"
                  height="150px"
                >
                  <v-card-subtitle>Extrakopf</v-card-subtitle>
                  <v-card-subtitle>+3€ & Tabak {{stepPrice2}}€</v-card-subtitle>
                </v-img>
              </v-card>
            </v-col>
          </v-row>
          <v-btn class="mt-3" block outlined @click="e6 = 4">Kein Extra</v-btn>
        </v-container>

        <v-btn color="primary" @click="e6 = 2; stepPrice3=0; ">Zurück</v-btn>
      </v-card>
    </v-stepper-content>
  </v-stepper>
</template>

<script>
import _ from 'lodash'
export default {
  name: "Configurator",
  props: {
    resetBool: Boolean,
  },
  methods: {
    reset() {

      this.e6 = 1;
            this.stepPrice1 = 0;
      this.stepPrice2 = 0;
      this.stepPrice3 = 0;
      this.stepPriceSum = 0;
      this.isModern = false;
      this.resetBool = false;
    },
  },
  computed: {
    price: function () {
      if (this.e6 == 4) return "Deine Shisha kostet";
      return "";
    },
   

    sumPrice: function () {
      // `this` points to the vm instance
      return this.stepPrice1 + this.stepPrice2 + this.stepPrice3;
    },
    currentPrice: function () {
      // `this` points to the vm instance
      if (this.isModern) return _.orderBy(this.modernPrice, 'price').reverse() ;
      if (!this.isModern) return _.orderBy(this.orientalPrice, 'price').reverse() ;


      return 0;
    },
  },
  data() {
    return {
      stepPrice1: 0,
      stepPrice2: 0,
      stepPrice3: 0,
      stepPriceSum: 0,
      isModern: false,
      open: true,
      e6: 1,
      images: {
        orientalisch: require("../assets/oriental.jpg"),
        modern: require("../assets/modern.jpg"),

        ice: require("../assets/ice.jpg"),
      },
      orientalPrice: [
        { name: "Al Waha", price: 2.6 },
        { name: "Chaos", price: 2.5 },
        { name: "Al Fakher", price: 2.9 },
        { name: "Nameless", price: 2.5 },
        { name: "Chillma", price: 2.9 },
      ],
      modernPrice: [
        { name: "187 ", price: 3.0 },
        { name: "Babos", price: 3.0 },
        { name: "Adalya", price: 2.6 },
        { name: "7Days", price: 2.0 },
        { name: "Aqua Menta", price: 2.9 },
        { name: "True Passion", price: 2.9 },
        { name: "Zomo", price: 1.9 },
      ],
      selectedPrice: [],
    };
  },
  watch: {
    resetBool: function () {
      this.reset();
    },
  },
};
</script>

<style scoped>
.v-stepper {
  box-shadow: none;
}
</style>